import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Router} from "@angular/router";

declare var require: any;
var data = require('../../assets/vocabulary.json');

export class User {
  public firstname: string;
  public email: string;
}

@Component({
  selector: 'app-page1',
  templateUrl: './page1.component.html',
  styleUrls: ['./page1.component.css']
})
export class Page1Component implements OnInit {
  userForm: FormGroup;
  user: User = new User();

  button: string = data.FORMS.BUTTONS.STEP_1;
  input1: string =  data.FORMS.PLACEHOLDERS.FIRSTNAME;
  input2: string = data.FORMS.PLACEHOLDERS.EMAIL;

  constructor(private fb: FormBuilder, private router: Router) { }

  ngOnInit() {
    this.buildForm();
  }

  buildForm() {
    this.userForm = this.fb.group({
        "firstname": [this.user.firstname, [
            Validators.required,
            Validators.pattern("^[a-zA-Z]+$")
        ]],
        "email": [this.user.email, [
            Validators.required,
            
        ]]
    });

        this.userForm.valueChanges
            .subscribe(data => this.onValueChange(data));

        this.onValueChange();
  }

  onValueChange(data?: any) {
    if (!this.userForm) return;
    let form = this.userForm;

    for (let field in this.formErrors) {
        this.formErrors[field] = "";

        let control = form.get(field);

        if (control && control.dirty && !control.valid) {
            let message = this.validationMessages[field];
            for (let key in control.errors) {
                this.formErrors[field] += message[key] + " ";
            }
        }
    }
  }

  onSubmit(userForm: FormGroup) {
    if(this.userForm.valid) {
      this.router.navigate(["/page2"], { queryParams:{firstname: userForm.value.firstname, email: userForm.value.email}});
    }
  }
  

  //  ,      
  formErrors = {
    "firstname": "",
    "email": ""
  };

  //    
  validationMessages = {
      "firstname": {
          "required": data.FORMS.VALIDATION.REQUIRED,
          "pattern": data.FORMS.VALIDATION.INVALID
      },
      "email": {
          "required": data.FORMS.VALIDATION.REQUIRED
      }
  };

}



