import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import {Router, ActivatedRoute} from "@angular/router";

declare var require: any;
var data = require('../../assets/vocabulary.json');

export class User2 {
  public firstname: string;
  public lastname: string;
  public email: string;
  public password: string;
  public number: number;
}

@Component({
  selector: 'app-page2',
  templateUrl: './page2.component.html',
  styleUrls: ['./page2.component.css']
})
export class Page2Component implements OnInit {
  userForm2: FormGroup;
  user2: User2 = new User2();

  input1;
  input3;
  button: string = (data).FORMS.BUTTONS.STEP_2;
  input1placeholder: string = data.FORMS.PLACEHOLDERS.FIRSTNAME;
  input2placeholder: string = data.FORMS.PLACEHOLDERS.LASTNAME;
  input3placeholder: string = data.FORMS.PLACEHOLDERS.EMAIL;
  input4placeholder: string = data.FORMS.PLACEHOLDERS.PASSWORD;
  input5placeholder: string = data.FORMS.PLACEHOLDERS.NUMBER;

  constructor(private fb: FormBuilder, private activatedRoute: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.buildForm();
    this.activatedRoute.queryParams.subscribe(params => {
      this.input1 = params['firstname'];
      this.input3 = params['email'];
    });
  }

  buildForm() {
    this.userForm2 = this.fb.group({
        "firstname": [this.user2.firstname, [
            Validators.required,
            Validators.pattern("^[a-zA-Z]+$")
        ]],
        "lastname": [this.user2.lastname, [
          Validators.required,
          Validators.pattern("^[a-zA-Z]+$")
        ]],
        "email": [this.user2.email, [
            Validators.required
        ]],
        "password": [this.user2.password, [
          Validators.required,
          Validators.minLength(6),
          Validators.maxLength(12)
        ]],
        "number": [this.user2.number, [
          Validators.required,
          Validators.pattern("[0-9]{7}")
        ]]
    });

        this.userForm2.valueChanges
            .subscribe(data => this.onValueChange(data));

        this.onValueChange();
  }

  onValueChange(data?: any) {
    if (!this.userForm2) return;
    let form = this.userForm2;

    for (let field in this.formErrors) {
        this.formErrors[field] = "";

        let control = form.get(field);

        if (control && control.dirty && !control.valid) {
            let message = this.validationMessages[field];
            for (let key in control.errors) {
                this.formErrors[field] += message[key] + " ";
            }
        }
    }
  }

  onSubmit(userForm2: FormGroup) {
    if(this.userForm2.valid){
      alert(data.REGISTRATION.SUCCESS);
      this.router.navigate(["/page1"], { relativeTo: this.activatedRoute });
    }
    else {
      alert(data.REGISTRATION.FAILURE);
    }
  }

  //  ,      
  formErrors = {
    "firstname": "",
    "lastname": "",
    "email": "",
    "password": "",
    "number": ""
  };

  //    
  validationMessages = {
      "firstname": {
          "required": data.FORMS.VALIDATION.REQUIRED,
          "pattern": data.FORMS.VALIDATION.INVALID
      },
      "lastname": {
        "required": data.FORMS.VALIDATION.REQUIRED,
        "pattern": data.FORMS.VALIDATION.INVALID
      },
      "email": {
        "required": data.FORMS.VALIDATION.REQUIRED
      },
      "password": {
        "required": data.FORMS.VALIDATION.REQUIRED,
        "minlength": data.FORMS.VALIDATION.INVALID,
        "maxlength": data.FORMS.VALIDATION.INVALID
      },
      "number": {
        "required": data.FORMS.VALIDATION.REQUIRED,
        "pattern": data.FORMS.VALIDATION.INVALID
      },
  };

}



